html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.5;
  font-family: "Source Sans 3", sans-serif;
  background-color: #f3f4f6;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.4rem;
}

p,
a {
  font-size: 1.6rem;
}

a {
  text-decoration: none;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  width: 100%;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 1400px) {
  .contenedor {
    max-width: 140rem;
  }
}

.input-container {
  position: relative; /* Contenedor de referencia */
  width: 100%;
  margin-bottom: 15px;
}

.input-container input {
  width: 100%;
  /* Añade espacio a la derecha para que el texto no tape el ícono */
  padding-right: 35px;
  /* (Añade aquí tus estilos de borde, fondo, etc.) */
}

.input-container i {
  position: absolute; /* Mueve el ícono libremente */
  right: 10px; /* Distancia desde el borde derecho */
  top: 69%; /* Lo centra verticalmente */
  transform: translateY(-50%); /* Ajuste vertical fino */
  color: #999; /* Color del ícono */
  pointer-events: none; /* Asegura que se pueda hacer clic en el input */
}

.boton-formulario {
  background-color: #2563eb;
  color: #ffffff;
  border: none;
  padding: 1rem 5rem;
  font-size: 1.8rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

header {
  background-color: #1f2937;
  padding: 2rem 0;
}
header .contenedor-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
header .contenedor-header h1 {
  color: #ffffff;
  font-size: 4rem;
}
header .contendor-logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
header .contendor-logo picture {
  width: 5rem;
  background-color: #e5e7eb;
  padding: 1rem;
  border-radius: 2rem;
}

.contenedor-nav {
  display: flex;
  gap: 2rem;
}
.contenedor-nav nav ul {
  display: flex;
  flex-direction: row;
  gap: 4rem;
  align-items: center;
}
.contenedor-nav nav ul li a {
  display: flex;
  color: #ffffff;
}
.contenedor-nav nav ul li a img {
  width: 3rem;
  margin-right: 1rem;
}
.contenedor-nav .boton a {
  color: #ffffff;
  background-color: #ef4444;
  padding: 0.5rem 2rem;
  border-radius: 1rem;
}

.main {
  margin-top: 5rem;
}
.main table {
  width: 100%;
  margin-top: 3rem;
  background-color: #ffffff;
  font-size: 1.8rem;
  color: #6b7280;
  border-collapse: collapse;
}
.main table thead {
  border: solid 1px #e5e7eb;
}
.main table thead tr {
  background-color: #6b7280;
}
.main table thead tr th {
  padding: 1rem;
  font-weight: 400;
  color: #ffffff;
}

.tabla-fila {
  border: solid 1px #e5e7eb;
  text-align: center;
}
.tabla-fila .tabla-dato {
  padding: 1rem;
  font-weight: 400;
}
.tabla-fila .tabla-dato-img {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
}
.tabla-fila .tabla-dato-img picture {
  width: 3rem;
  cursor: pointer;
}

.contenedor-agregar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.contenedor-agregar a {
  background-color: #16a34a;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
  padding: 0.5rem 2rem;
  color: #ffffff;
  border-radius: 0.5rem;
}
.contenedor-agregar a img {
  width: 2rem;
  margin-top: 0.5rem;
}

.contenedor-formulario {
  background-color: #ffffff;
}
.contenedor-formulario .form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contenedor-formulario .form .formulario {
  width: 90%;
}
.contenedor-formulario .form .formulario form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 1rem;
  width: 100%;
}
.contenedor-formulario .form .formulario form label {
  font-size: 1.6rem;
}
.contenedor-formulario .form .formulario form input {
  width: 100%;
  height: 5rem;
  padding: 1rem;
  font-size: 1.6rem;
  border: solid 1px #d3d3d3;
  border-radius: 1rem;
}

.formulario-boton {
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-items: center;
}

.header-formulario {
  background-color: #1f2937;
}
.header-formulario h2 {
  color: #ffffff;
  padding: 2rem;
  margin-bottom: 3rem;
}